import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'


import warmUp from  '@/components/warmUp/warmUp'

import VoteSchool from '@/components/vote/VoteSchool' //
import VoteClass from '@/components/vote/VoteClass' //
import Contestant from '@/components/vote/Contestant' //
import TalentSpace from '@/components/vote/TalentSpace' //
import SearchResult from '@/components/vote/SearchResult' //

import personalCenter from '@/components/personalCenter/personalCenter' //
import loginOn from '@/components/personalCenter/loginOn' //


import Recharge from  '@/components/Recharge/Recharge' //
import ShowRecharge from '@/components/Recharge/ShowRecharge' //

import  aboutUcan from   '@/components/aboutMe/aboutUcan' //

import  partnerSchool from   '@/components/partnerSchool/partnerSchool' //
import  everyPartnerSchool from   '@/components/partnerSchool/everyPartnerSchool'  //

import  sponsor from   '@/components/sponsor/sponsor' //

import  paySuccess from   '@/components/Recharge/paySuccess' //
import  payFail from   '@/components/Recharge/payFail' //

// import  fileUploader from '@/components/fileUploader/fileUploader'   //
Vue.use(Router)

export default new Router({

  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      meta: {title: "momo"}
    },
    {
      path:'/warmUp',
      name:'warmUp',
      component:warmUp,
      meta:{title: ""}
    },
    {
      path:'/personalCenter',
      name:'personalCenter',
      component:personalCenter,
      meta:{title: ""}
    },
    {
      path:'/loginOn',
      name:'loginOn',
      component:loginOn,
      meta:{title: ""}
    },
    {
      path: '/voteSchool',
      name: 'voteSchool',
      component: VoteSchool,
      meta: {title: ""}
    },
    {
      path: '/voteClass/:id',
      name: 'voteClass',
      component: VoteClass,
      meta: {title: ""}
    },

    {
      path: '/contestant/:id',
      name: 'contestant',
      component: Contestant,
      meta: {title: ""}
    },

    {
      path: '/searchResult',
      name: 'searchResult',
      component: SearchResult,
      meta:{title:""}
    },

    {
      path: '/talentSpace/:particiNum',
      name: 'talentSpace',
      component: TalentSpace,
      meta: {title: ""}
    },
    {
      path: '/talentSpace/Recharge',
      name: 'Recharge',
      component: Recharge,
      meta: {title: ""}
    },
    {
      path:'/ShowRecharge',
      name:'ShowRecharge',
      component:ShowRecharge,
      meta:{title: ""}
    },
    {
      path:'/aboutUcan',
      name:'aboutUcan',
      component:aboutUcan,
      meta:{title: ""}
    },
    {
      path: '/partnerSchool',
      name: 'partnerSchool',
      component: partnerSchool,
      meta:{title: ""}
    },
    {
      path: '/partnerSchool/everyPartnerSchool/:id',
      name: 'everyPartnerSchool',
      component: everyPartnerSchool,
      meta:{title: ""}
    },
    {
      path: '/sponsor',
      name: 'sponsor',
      component: sponsor,
      meta:{title: ""}
    },
    {
      path: '/paySuccess',
      name: 'paySuccess',
      component: paySuccess,
      meta:{title: ""}
    },
    {
      path: '/payFail',
      name: 'payFail',
      component: payFail,
      meta:{title: ""}
    }


]

})



